import { Component, ChangeDetectionStrategy, ViewChild, TemplateRef, NgZone } from '@angular/core';
import { ViewEncapsulation } from '@angular/core';
import { CompleterService, CompleterData } from 'ng2-completer';
import { SelectClientComponent } from './select-client/select-client.component';
import { AppTranslationModule } from '../../../app.translation.module';
import {Router} from '@angular/router';
import { NotificationsService } from 'angular2-notifications';
//import moment = require('moment');
import * as moment from 'moment';
import {
  startOfDay,
  endOfDay,
  subDays,
  addDays,
  endOfMonth,
  isSameDay,
  isSameMonth,
  isSameWeek,
  addHours,
} from 'date-fns';
import { Subject } from 'rxjs/Subject';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import {
  CalendarEvent,
  CalendarEventAction,
  CalendarEventTimesChangedEvent,
} from 'angular-calendar';

import { CalendarService, Calendar } from '../../../services/calendars.service';
import { ServicesService, Service } from '../../../services/services.service';
import { ClientsService, Client } from '../../../services/clients.service';
import { AppointmentService, Appointment } from '../../../services/appointment.service';
import { OpeningHoursService, OpeningHours } from '../../../services/openinghours.service';
import { SlimLoadingBarService } from 'ng2-slim-loading-bar';
import { User } from '../../../services/users.service';
import { LanguageService } from '../../../services/language.service';

import { CalendarSchedulerViewComponent } from './calendar-custom/calendar-scheduler-view.component';

const colors: any = {
  red: {
    primary: '#ad2121',
    secondary: '#FAE3E3'
  },
  blue: {
    primary: '#1e90ff',
    secondary: '#D1E8FF'
  },
  yellow: {
    primary: '#e3bc08',
    secondary: '#FDF1BA'
  }
};


declare var test: any;

@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  selector: 'appointments-holder',
  templateUrl: './appointments.html',
  host: {'(window:keydown)': 'hotkeys($event)'},
  styleUrls:['angular-calendar.css','appointments.scss','calendar-custom/styles.scss'],
  encapsulation: ViewEncapsulation.None
})
export class AppointmentComponent {


  @ViewChild('modalContent') modalContent: TemplateRef<any>;

  view: string = 'day';

  viewDate: Date = new Date();

  tempviewDate: Date = new Date();

  CalendarServiceNew: any = [];


  modalData: {
    action: string,
    event: CalendarEvent,
  };

  actions: CalendarEventAction[] = [{
    label: '<i class="fa fa-fw fa-pencil"></i>',
    onClick: ({event}: {event: CalendarEvent}): void => {
      this.handleEvent('Edited', event);
    }
  }, {
    label: '<i class="fa fa-fw fa-times"></i>',
    onClick: ({event}: {event: CalendarEvent}): void => {
      this.events = this.events.filter(iEvent => iEvent !== event);
      this.handleEvent('Deleted', event);
    }
  }];

  refresh: Subject<any> = new Subject();
  
  tempEvents: any[];
  events: any[];

  activeDayIsOpen: boolean = true;
  dropEvent: boolean = false;

  currentUser: any; 
  calendars: any;
  services: any;
  clients: any;
  selectedService: string;
  selectedCalender: string;
  selectedCalendarName: string;
  selectedCalendarImage: string;
  selectedClient: string;
  appointments: any;
  busyshow: boolean = true;
  openingHours: any;
  startHour: string;
  startMinute: string;
  endHour: string;
  endMinute: string;
  dayString; string;
  dayStart: string;
  dayEnd: string;
  clickedDate: Date;
  eventEditTitle: string = '';
  currentlang: string;
  public loading: boolean; 
  language;

  curSegmentTime: any;

  options = {
      position:["top", "right"],
      timeOut:3000,
      lastOnBottom: true,
      clickToClose: true,
      animation: 'scale',
      showProgressBar: false,
      maxLength: 100
    }

    externalEvents: any = [
    {
      title: 'Event 1',
      color: colors.yellow,
      start: new Date(),
      draggable: true
    },
    {
      title: 'Event 2',
      color: colors.blue,
      start: new Date(),
      draggable: true
    }
  ];

  constructor(private modal: NgbModal, 
              private calendarService: CalendarService, 
              private servicesService: ServicesService,
              private clientService: ClientsService,
              private appointmentService: AppointmentService,
              private openingHoursService: OpeningHoursService,
              private completerService: CompleterService,
              private slimLoader: SlimLoadingBarService,
              private zone: NgZone,
              private translate: AppTranslationModule,
              private router: Router,
              private _service: NotificationsService,
              private _langService: LanguageService) {

    test = this;

    $(".cal-hour-segment").removeClass("active");

    $(document).on("click",".cal-hour-segment",function() {
      $(".cal-hour-segment").removeClass("active");
      $(this).addClass('active');
    });

    this.currentUser = JSON.parse(localStorage.getItem('currentUser'));
     
    this.startHour = '9';
    this.startMinute = '30';
    this.endHour = '17';
    this.endMinute = '30';

    this.currentlang = localStorage.getItem('lang');
    this.translate.setDefaultLang(localStorage.getItem('lang'));
    if(localStorage.getItem('lang')=='de'){
        this._langService.getDEJSON().then(data => {
        // //console.log("12345678", data);  
        this.language=data;
      })
    } else {
      this._langService.getENJSON().then(data => {
        // //console.log("12345678", data);  
        this.language=data;
      })
    }


  }

  hotkeys(event){
     // event.key
     // selectedService

     var selected_service = '';
     var selected_calendar = '';

     for(var i=0; i< this.services.length; i++){
       if(this.services[i].shortKey.toString().toLocaleLowerCase()==event.key) {

         selected_service = this.services[i]._id;

       }
     }

     for(var i=0; i< this.calendars.length; i++){
       if(this.calendars[i].shortKey.toString().toLocaleLowerCase()==event.key) {
         // this.selectedCalender = this.calendars[i]._id;
         
         selected_calendar = this.calendars[i]._id;

       }
     }

      if(selected_calendar) {

        // // //console.log("change Calendar");

       localStorage.setItem('selected_calendar', selected_calendar);

       // this.checkCalendarSelecter(selected_calendar);

       // this.onChangeCalender(selected_calendar);
       

     } else if(selected_service) {
       this.onChangeService(selected_service);
     }

     if(event.keyCode==37) {
       // navigating to previous
       document.getElementById("prevNavMove").click();
     }

     if(event.keyCode==39) {
       // navigating to next
       document.getElementById("nextNavMove").click();
     }

     if(event.keyCode==38) {
       var curindex = this.getServiceIndex(this.selectedService);

       if(typeof this.services[(curindex - 1)]!='undefined') {
         // this.selectedService = this.services[(curindex - 1)]._id;
         this.onChangeService(this.services[(curindex - 1)]._id);
       } else {
         this.onChangeService("");
       }

       event.preventDefault();
     }

     if(event.keyCode==40) {
        
        var curindex = this.getServiceIndex(this.selectedService);

        if(typeof this.services[(curindex + 1)]!='undefined') {
          this.onChangeService(this.services[(curindex + 1)]._id);
        }

        event.preventDefault();
     }

   }

  getServiceIndex(service) {
    var index = 0;
    for(var i=0; i< this.services.length; i++){
       if(this.services[i]._id==service) {
         index = i;
       }
     }

     return index;
  }

  dayClicked({date, events}: {date: Date, events: CalendarEvent[]}): void {

    this.clickedDate = date;
    if(localStorage.getItem('lang')==="en"){
      this.eventEditTitle = "Edit Event";
    } else {
      this.eventEditTitle = "Termin Bearbeiten";
    }

    if (isSameMonth(date, this.viewDate)) {
      if (
        (isSameDay(this.viewDate, date) && this.activeDayIsOpen === true) ||
        events.length === 0
      ) {
        this.activeDayIsOpen = false;

      } else {
        this.viewDate = date;
      }

      //forcefull changes
      this.tempviewDate = date;

    }

    this.tempEvents = [];

    for(var i=0; i< this.events.length; i++){
      if(isSameMonth(date, this.events[i].start) && isSameDay(date, this.events[i].start)) {
         
         this.tempEvents.push(this.events[i]);
      }
    }

    this.refresh.next();

  }

  ngOnInit() {
    
    if(this.currentUser==null || this.currentUser.user.role !== "company" || this.currentUser.user.active!== true){
        this.router.navigate(['']);
    }

    this.initial();

    var THIS = this;

    setInterval(function() {
      THIS.checkCalendarSelecter();
    }, 1000);

  }

  checkCalendarSelecter(cal: any = null) {

    if(typeof this.calendars!='undefined' && this.calendars.length > 0 && typeof this.events!='undefined') {

      if(cal) {
        this.selectedCalender = cal;
      }

      if(typeof this.selectedCalender=="undefined" || this.selectedCalender!=localStorage.getItem('selected_calendar')) {

        if(localStorage.getItem('selected_calendar')) {

          var THIS = this;

          for(var k=0; k < this.calendars.length; k++) {
            if(localStorage.getItem('selected_calendar')==this.calendars[k]._id) {
              this.selectedCalendarName = this.calendars[k].description;
              this.selectedCalendarImage = this.calendars[k].image;
            }
          }

          this.loading = true;
          // this.selectedCalender=localStorage.getItem('selected_calendar');

          setTimeout(function() {
            THIS.loading = false;
            THIS.onChangeCalender(localStorage.getItem('selected_calendar'));
          }, 1000);

        }

      }
      
    }

  }

  initial(refresh_calendar: boolean = false) {

    this.loading = true;
    if(!this.clickedDate) {
      this.clickedDate = new Date();
    }


    this.calendarService.getStatusCalendars(this.currentUser.user.companyID).then((data) => {
        this.calendars = data;
        this.servicesService.getStatusServices(this.currentUser.user.companyID).then((data) => {
          this.services = data;
          
          for(var p=0;p < this.services.length; p++) {
            this.services[p].draggable = true;
            this.services[p].showTime = false;
            this.services[p].height = 35;
          }

          this.clientService.getAllClients().then((data) => {
            this.clients = data;

            

            this.openingHoursService.getOpeningHourse(this.currentUser.user.companyID)
              .then((data) => {
                this.openingHours = data;
                this.loading = false;



              // // //console.log(this.openingHours);
              this.appointmentService.getAppointments(this.currentUser.user.companyID)
                .then((data) => {
                  
                    this.appointments = data;
                    this.analyAppointment(this.appointments, refresh_calendar);
                    
                    this.weekSelected();
                    // // //console.log(this.appointments);
                   
                });
              });
          });
       });
      });

      this.slimLoader.start();
  }

  analyAppointment(appointment, refresh_calendar) {

    this.tempEvents = [];
    this.events = [];
    if(appointment.length==0) return;
    for(var i=0; i< appointment.length; i++){
      // let t = "Calendar: ";
      
      let t = "";

      for(var j=0; j < this.calendars.length; j++)
      {
        if (this.calendars[j]._id == appointment[i].calendarID)
        {
          // t += this.calendars[j].description;
        }
      }

      for (var j=0; j < this.clients.length; j++) {
        if(this.clients[j]._id == appointment[i].clientID)
        {
          t += this.clients[j].lastName;
        }
      }
      
      t += " (" + this.getTimeFormatted(appointment[i].start)+")<br>";
      

      for (var j=0; j < this.services.length; j++)
      {
        if(this.services[j]._id == appointment[i].serviceID)
        {
          t += "Service: "+this.services[j].description+"<br>";
        }
      }
      
      for (var j=0; j < this.clients.length; j++) {
        if(this.clients[j]._id == appointment[i].clientID)
        {
          t += "Client: "+this.clients[j].firstName+" "+this.clients[j].lastName;
        }
      }

      if(appointment[i].type == "Busy"){
        t="Busy";
      }

      let result = this.clients.filter(function( obj ) {
        return obj._id === appointment[i].clientID;
      });

      if(result.length==0) {
        var c_email = '';
        var c_id = '';
      } else {
        c_email = result[0].email;
        c_id = result[0]._id;
      }

      this.events.push({
        _id: appointment[i]._id,
        selectedCalender: appointment[i].calendarID,
        selectedClientEmail: c_email,
        selectedClient: c_id,
        type: appointment[i].type,
        selectedService: appointment[i].serviceID,
        title: t,
        start: new Date(appointment[i].start),
        end: new Date(appointment[i].end),
        color: colors.red,
        draggable: false,
        resizable: {
          beforeStart: true,
          afterEnd: true,
        }
      });

    }

    if(refresh_calendar) {
      this.onChangeCalender(this.selectedCalender);
    }

    this.refresh.next();
  }



  eventTimesChanged({event, newStart, newEnd}: CalendarEventTimesChangedEvent): void {
    event.start = newStart;
    event.end = newEnd;
    this.handleEvent('Dropped or resized', event);
    this.refresh.next();
  }

  handleEvent(action: string, event): void {
    // this.modalData = {event, action};
    // this.modal.open(this.modalContent, {size: 'lg'});
    // this.tempEvents = [];

    // // //console.log(action);
    // // //console.log(event);


    // this.selectedCalender = event.selectedCalender;
    // this.selectedService = event.selectedService;
    // if(action !== 'Clicked')
    // {
    //   this.saveAppointment(event);
    // }
    // this.tempEvents.push(event);

    this.editAppointment(event);

  }

  addBusyTime() {

    let calendarName;

    for(var i=0; i< this.calendars.length; i++){
      if(this.selectedCalender == this.calendars[i]._id){
        calendarName = this.calendars[i].description;
      }
    }
    
    if(calendarName === undefined) {
      this.showErrorToast(this.language.message.info, this.language.message.selectcalendar);
      return;
    } else {

      if(localStorage.getItem('lang')==="en"){
        this.eventEditTitle = "Add Busy Time (Calendar: "+calendarName+")";
      } else {
        this.eventEditTitle = "Belegtzeit hinzufügen (Kalendar: "+calendarName+")";
      }
    
      this.tempEvents = [];

      this.tempEvents.push({
        title: 'Busy',
        start: startOfDay(this.clickedDate),
        end: endOfDay(this.clickedDate),
        color: colors.red,
        type: 'Busy',
        editing: true,
        draggable: false,
        resizable: {
          beforeStart: true,
          afterEnd: true,
        }
      });

    }

  }

  selectClient() {
    

    const activeModal = this.modal.open(SelectClientComponent, { size: 'lg' });
    activeModal.componentInstance.modalHeader = 'Select Client';
    activeModal.componentInstance.modalType = 'add';
    activeModal.componentInstance.modalData = {};
    activeModal.result.then((result) => {
      
      if ( result.length > 0 ) {
        this.zone.run(() => {
          this.tempEvents[(this.tempEvents.length-1)].selectedClientEmail = result[0].email;
          this.tempEvents[(this.tempEvents.length-1)].selectedClient = result[0]._id;
          if(this.dropEvent) {
            this.saveAppointment(this.tempEvents[(this.tempEvents.length-1)]);
          }
        });
        this.refresh.next();
      }
        
    })
     .catch(() => {
       if(this.dropEvent) {
         // this.tempEvents.splice((this.tempEvents.length-1), 1);
         this.selectedService = undefined;
         this.refreshGetEvents();
       }
     });

  }

  draggingService(itm) {
    this.dropEvent = false;
    if(this.curSegmentTime) {
      
      this.selectedService = itm._id;

      itm.showTime = true;
      itm.start = moment(this.curSegmentTime).format("HH:mm");
      itm.end = moment(this.curSegmentTime).add(itm.duration, 'minutes').format("HH:mm");
      itm.height = (((itm.duration / 5)) * 17);
    } else {
      itm.showTime = false;
      itm.height = 35;
    }

    // //console.log(itm);
  }

  segmentHover(dta) {
    if(dta.isHover) {
      this.curSegmentTime = dta.segment;
    } else {
      this.curSegmentTime = false;
    }
  }

  addEvent(fromweekdrop = false): void {

    let calendarName;
    let serviceName;
    let serviceDuration;
    let servicePrice;

    for(var i=0; i< this.calendars.length; i++){
      if(this.selectedCalender == this.calendars[i]._id){
        // //console.log(this.calendars[i]._id);
        // //console.log(this.selectedCalender);
        calendarName = this.calendars[i].description;
      }
    }

    for(var i=0; i< this.services.length; i++){
      if(this.selectedService == this.services[i]._id){
        serviceName = this.services[i].description;
        serviceDuration = this.services[i].duration;
        servicePrice =  this.services[i].price;
      }
    }

    if(calendarName === undefined) {
      this.showErrorToast(this.language.message.info, this.language.message.selectcalendar);
      return;
    } else if(serviceName === undefined){
      this.showErrorToast(this.language.message.info, this.language.message.selectservice);
      return;
    } else {

      // changing to day view with selected day
      if(!fromweekdrop) {
        this.daySelected();
      }

      if(localStorage.getItem('lang')==="en"){
        this.eventEditTitle = "Add New Appointment (Calendar: "+calendarName+
                            ", Service: "+serviceName+
                            ", Price: "+servicePrice+
                            ", Duration: "+serviceDuration+
                            ")";
      } else {
        this.eventEditTitle = "Termin erstellen(Kalendar: "+calendarName+
                            ", Service(DE): "+serviceName+
                            ", Preis: "+servicePrice+
                            ", Dauer: "+serviceDuration+
                            ")";
      }

      // this.tempEvents = [];

      this.tempEvents.push({
        title: 'New Appointment',
        start: startOfDay(this.clickedDate),
        end: endOfDay(this.clickedDate),
        selectedClientEmail : this.language.appointment.selectclient,
        selectedClient : this.language.appointment.selectclient,
        color: colors.blue,
        type: 'Appointment',
        editing: true,
        draggable: false,
        resizable: {
          beforeStart: true,
          afterEnd: true,
        }
      });
      
    }


  }

  saveAppointment(e): void {
      let saveData = new Appointment();
      let clientID: any;
      
      if(e.selectedClient === this.language.appointment.selectclient) {
        this.showErrorToast(this.language.message.info, this.language.message.selectclient); return;
      }

      let result = this.clients.filter(function( obj ) {
        return obj.email === e.selectedClient;
      });

      // //console.log(result);

      if ( result.length === 0 ) {
        clientID = e.selectedClient;
      } else {
        clientID = result[0]._id;
      }

      // //console.log(clientID);

      this.dropEvent = false;

      if(e.type == 'Busy'){
        saveData.companyID = this.currentUser.user.companyID;
        saveData.calendarID = this.selectedCalender;
        // saveData.serviceID = "";
        // saveData.clientID = "";
        saveData.start = e.start;
        saveData.end = e.end;
        saveData.type = "Busy";
      } else {
        saveData.companyID = this.currentUser.user.companyID;
        saveData.calendarID = this.selectedCalender;
        saveData.serviceID = this.selectedService;
        saveData.clientID = clientID;
        saveData.start = e.start;
        saveData.end = e.end;
        saveData.type = "Appointment";
      }

      if (e._id === undefined) {

        if(this.checkExistingEventTime(saveData.start)) {
          this.appointmentService.create(saveData).then(appointment => {
              this.initial(true);
              this.refresh.next();
              this.showSuccessToast(this.language.message.success, this.language.message.appointmentcreated);
          }, error => {
            
           this.selectedService = undefined;
           this.refreshGetEvents();

            this.showErrorToast(this.language.message.info, this.language.message.alreadyhaaveappointment); return;
          });
        } else {
          this.showErrorToast(this.language.message.info, this.language.message.alreadyhaaveappointment);
        }
        
      } else {
        this.appointmentService.update(saveData, e._id, this.currentUser.user.companyID).then(appointment => {
          this.initial(true);
          this.refresh.next();
          this.showSuccessToast(this.language.message.success, this.language.message.appointmentupdated);
        });
      }

  }

  deleteAppointment(e, index): void {
     if (window.confirm(this.language.message.deleteconfirm)) {
        this.appointmentService.delete(e._id, this.currentUser.user.companyID);
        if(index>-1){
          for(var i=0; i<this.events.length; i++){
            if(this.events[i]._id === e._id){
              this.events.splice(i, 1)
            }
          }
          
          // this.tempEvents.splice(index, 1);
          
          // this.refresh.next();
          this.refreshGetEvents();

          this.showSuccessToast(this.language.message.success, this.language.message.appointmentremoved);
        }
     } 
     
  }

  onChangeCalender(calender) {
    // //console.log("selected", calender);
    this.selectedCalender = calender;
    this.tempEvents = [];

    for(var i=0; i<this.events.length; i++){
      // if ( this.selectedService === undefined ) {
        if ( this.view === 'month') {
          if ( this.selectedCalender === this.events[i].selectedCalender && 
                isSameMonth(this.viewDate, this.events[i].start) ) {
            this.events[i].editing = false;
            this.tempEvents.push(this.events[i]);
          }
        }
        if ( this.view === 'week') {
          if ( this.selectedCalender === this.events[i].selectedCalender && 
                isSameWeek(this.viewDate, this.events[i].start) ) {
            this.events[i].editing = false;
            this.tempEvents.push(this.events[i]);
          }
        }
        if ( this.view === 'day') {
          if ( this.selectedCalender === this.events[i].selectedCalender && 
                isSameDay(this.viewDate, this.events[i].start) ) {
            this.events[i].editing = false;
            this.tempEvents.push(this.events[i]);
          }
        }


    }

    // //console.log(this.tempEvents, "Events");
    

    this.refresh.next();
  }

  getDateFormatted(time: string) {
    return moment(time).format("DD-MM-YYYY");
  }

  editAppointment(event: any) {
    event.editing = true;
    if(event.selectedService) {
      this.selectedService = event.selectedService;
    }
    this.refresh.next();
  }

  getTimeFormatted(time: string) {
    return moment(time).format("HH:mm");
  }

  getEventService(event) {
    var THIS = this;

    if(typeof event.serviceName=="undefined") {
      var service = this.services.filter(function( obj ) {
          return obj._id == event.selectedService;
      });
      
      if(service.length > 0) {
        event.serviceName = service[0].description + "<br/> <b>" + service[0].duration + " Minutes </b>";
      }

    }


    return event.serviceName;
  }

  getClientHTML(client) {
    var THIS = this;

    var html = '';

    if(client) {
      var client = this.clients.filter(function( obj ) {
          return obj._id == client;
      });
      
      if(client.length > 0) {
        // event.serviceName = service[0].description + "<br/> <b>" + service[0].duration + " Minutes </b>";
        html += '' + client[0].email + '<br/>' + client[0].firstName + ' ' + client[0].lastName + '<br/>' + client[0].phoneNumber
      }

    }


    return html;
  }

  onChangeService(service) {
    this.selectedService = service;
    this.refresh.next();
  }

  // eventDropped($event, segment, data) {
  //   // //console.log("Dropped In Segment");
  //   // //console.log($event);
  //   // //console.log(segment);
  //   // //console.log(data);
  //   // if($event)
  // }

  checkExistingEventTime(start) {
    var starttime = start;
    var endtime = null;

    var serviceTemp = null;

    var allow = true;

    for(var i=0; i< this.services.length; i++){
      var serv = null;
      
      serv = this.selectedService;

      if(serv == this.services[i]._id){
        serviceTemp = this.services[i];
        this.services[i].showTime = false;
      }
    }

    if(typeof serviceTemp!='undefined') {

      var enddate = moment(starttime).add(serviceTemp.duration, 'minutes').toDate();

      // this.tempEvents[0].end = enddate;
      endtime = enddate;

    }

    // console.log(starttime,"starttime");
    // console.log(endtime,"endtime");
    // console.log(this.tempEvents,"events");

    $.each(this.tempEvents, function(ind, elm) {

      if(this._id) {
        
        if(this.start.toString()==starttime.toString() || this.end.toString()==endtime.toString()) {
          allow = false;
        }

        if((this.start > starttime  && this.start < endtime) || (this.end > starttime  && this.end < endtime)) {
          allow = false;
        }

        if(starttime > this.start && endtime < this.end) {
          allow = false;
        }

        if(starttime < new Date(moment(this.viewDate).format("DD-MM-YYYY") + " " + this.dayStart)) {
          allow = false;
        }

        if(endtime > new Date(moment(this.viewDate).format("DD-MM-YYYY") + " " + this.dayEnd)) {
          allow = false;
        }

        if(starttime < this.start && endtime > this.end) {
          allow = false;
        }
        
      }

    });

    return allow;
  }

  eventDroppedOnTime($evnt) {

    // //console.log($evnt, "Dropped");

    var event = $evnt.event;
    var newStart = $evnt.hour;

    var allow = false;

    this.dropEvent = true;

    if(this.checkExistingEventTime(newStart)) {
      this.selectedService = event['_id'];
      this.clickedDate = newStart;
      this.viewDate = newStart;
      this.setStartEndHour();
      this.addEvent(true);

      var THIS = this;

      this.curSegmentTime = false;


      setTimeout(function() {
        var indx = (THIS.tempEvents.length-1);

        THIS.tempEvents[indx].start = newStart;
        THIS.dropEvent = true;
        
        setTimeout(function() {
          THIS.startDateChange(THIS.tempEvents[indx], null);
          setTimeout(function() {
            THIS.startDateChange(THIS.tempEvents[indx], null);
            THIS.selectClient();
          }, 200);
          // THIS.startDateChange(THIS.tempEvents[0], newStart);
        }, 100);
      }, 30);
    } else {
      this.showErrorToast(this.language.message.info, this.language.message.alreadyhaaveappointment);
    }

  }

  eventDropped({
    event,
    newStart,
    newEnd
  }: CalendarEventTimesChangedEvent): void {
    // alert();
    // //console.log(event);
    // //console.log(newStart);
    this.selectedService = event['_id'];

    if(this.checkExistingEventTime(newStart)) {

      this.clickedDate = newStart;
      this.viewDate = newStart;
      this.setStartEndHour();
      this.addEvent();

      var THIS = this;
      setTimeout(function() {
        THIS.tempEvents[(THIS.tempEvents.length-1)].start = newStart;
        THIS.dropEvent = true;
        
        setTimeout(function() {

          THIS.startDateChange(THIS.tempEvents[(THIS.tempEvents.length-1)], null);
          
          setTimeout(function() {
            THIS.startDateChange(THIS.tempEvents[(THIS.tempEvents.length-1)], null);
            THIS.selectClient();
          }, 500);

          // THIS.startDateChange(THIS.tempEvents[0], newStart);
        }, 500);
      }, 500);

    } else {
      this.showErrorToast(this.language.message.info, this.language.message.alreadyhaaveappointment);
    }

  }

  showBusy(e) {
    if ( e.target.checked ) {
      this.busyshow = true;
      this.selectedService = undefined;
      this.refresh.next();
    } 
    else {
      this.busyshow = false;
    }
  }

  daySelected() {

    this.view = 'day';

    this.viewDate = this.tempviewDate;

    this.setStartEndHour();

    this.refreshGetEvents();

    // removing active from hours
    $(".cal-hour-segment").removeClass("active");

  }

  weekSelected() {
    this.view = 'week';
    // this.refreshGetEvents();

    this.viewDate = this.tempviewDate;

    this.setStartEndHour();

    this.refreshGetEvents();

    // removing active from hours
    $(".cal-hour-segment").removeClass("active");
  }

  monthSelected() {
    this.view = 'month';
    this.refreshGetEvents();
  }
  nextDayClicked() {
    
    // //console.log("NEXT");

    this.setStartEndHour();
    this.refreshGetEvents();


  }
  prevDayClicked() {
    this.setStartEndHour();
    this.refreshGetEvents();
  }
  toDayClicked() {
    this.setStartEndHour();
    this.refreshGetEvents();
  }
  setStartEndHour() {

    switch (this.viewDate.getDay().toString()) {
      case '0': 
          this.dayStart = this.openingHours['sunfrom'];
          this.dayEnd = this.openingHours['sunto']; 
        break;
      case '1': 
          this.dayStart = this.openingHours['monfrom'];
          this.dayEnd = this.openingHours['monto'];
        break;
      case '2': 
          this.dayStart = this.openingHours['tuefrom'];
          this.dayEnd = this.openingHours['tueto'];
        break;
      case '3':
          this.dayStart = this.openingHours['wedfrom'];
          this.dayEnd = this.openingHours['wedto'];
        break;
      case '4':
          this.dayStart = this.openingHours['thufrom'];
          this.dayEnd = this.openingHours['thuto'];
        break;
      case '5':   
          this.dayStart = this.openingHours['frifrom'];
          this.dayEnd = this.openingHours['frito'];
        break;
      case '6': 
          this.dayStart = this.openingHours['satfrom'];
          this.dayEnd = this.openingHours['satto'];
        break;
    }
    this.startHour = this.dayStart.split(":")[0];
    this.startMinute = this.dayStart.split(":")[1];
    this.endHour = this.dayEnd.split(":")[0];
    this.endMinute = this.dayEnd.split(":")[1];

    if(this.view=== 'day' || this.view=== 'week') {
      this.tempviewDate = this.viewDate;
      this.clickedDate = this.viewDate;
    }

  }

  refreshGetEvents() {
    this.tempEvents = [];
    for(var i=0; i<this.events.length; i++){
      if ( this.selectedCalender === undefined ) {
        if ( this.view === 'month') {
          if ( (this.selectedService === this.events[i].selectedService || 1==1) && 
                isSameMonth(this.viewDate, this.events[i].start) ) {
            this.tempEvents.push(this.events[i]);
          }
        }
        if ( this.view === 'week') {
          if ( (this.selectedService === this.events[i].selectedService || 1==1) && 
                isSameWeek(this.viewDate, this.events[i].start) ) {
            this.tempEvents.push(this.events[i]);
          }
        }
        if ( this.view === 'day') {
          if ( (this.selectedService === this.events[i].selectedService || 1==1) && 
                isSameDay(this.viewDate, this.events[i].start) ) {
            this.tempEvents.push(this.events[i]);
          }
        }
      } else if ( this.selectedService === undefined ) {
        if ( this.view === 'month') {
          if ( this.selectedCalender === this.events[i].selectedCalender && 
                isSameMonth(this.viewDate, this.events[i].start) ) {
            this.tempEvents.push(this.events[i]);
          }
        }
        if ( this.view === 'week') {
          if ( this.selectedCalender === this.events[i].selectedCalender && 
                isSameWeek(this.viewDate, this.events[i].start) ) {
            this.tempEvents.push(this.events[i]);
          }
        }
        if ( this.view === 'day') {
          if ( this.selectedCalender === this.events[i].selectedCalender && 
                isSameDay(this.viewDate, this.events[i].start) ) {
            this.tempEvents.push(this.events[i]);
          }
        }
      } else {
        if ( this.view === 'month') {
          if ( this.selectedCalender === this.events[i].selectedCalender && 
                (this.selectedService === this.events[i].selectedService || 1==1) &&
                isSameMonth(this.viewDate, this.events[i].start) ) {
            this.tempEvents.push(this.events[i]);
          }
          if ( this.selectedCalender === this.events[i].selectedCalender && 
                this.events[i].title === 'Busy' &&
                isSameMonth(this.viewDate, this.events[i].start) ) {
             this.tempEvents.push(this.events[i]);
          }
        }
        if ( this.view === 'week') {
          if ( this.selectedCalender === this.events[i].selectedCalender && 
                (this.selectedService === this.events[i].selectedService || 1==1) &&
                isSameWeek(this.viewDate, this.events[i].start) ) {
            this.tempEvents.push(this.events[i]);
          }
          if ( this.selectedCalender === this.events[i].selectedCalender && 
                this.events[i].title === 'Busy' &&
                isSameWeek(this.viewDate, this.events[i].start) ) {
             this.tempEvents.push(this.events[i]);
          }

          //console.log(this.tempEvents);

        }
        if ( this.view === 'day') {
          if ( this.selectedCalender === this.events[i].selectedCalender && 
                (this.selectedService === this.events[i].selectedService || 1==1) &&
                isSameDay(this.viewDate, this.events[i].start) ) {
            this.tempEvents.push(this.events[i]);
          }
          if ( this.selectedCalender === this.events[i].selectedCalender && 
                this.events[i].title === 'Busy' &&
                isSameDay(this.viewDate, this.events[i].start) ) {
             this.tempEvents.push(this.events[i]);
          }
        }
      }
    }
    this.refresh.next();
  }

  ngOnDestroy() {
  }

  showSuccessToast(title, content){
    this._service.success(
      title,
      content,
      this.options
    )
  }

  showErrorToast(title, content){
    this._service.info(
      title,
      content,
      this.options
    )
  }

  startDateChange(event,startDate){

    //var enddate =  moment(startDate).add(2, 'hours');

    var _THIS = this;
    this.zone.run(() => {

      
      _THIS.refresh.next();

      var serviceTemp = null;

      for(var i=0; i< this.services.length; i++){
        var serv = null;
        if(this.selectedService) {
          serv = this.selectedService;
        } else {
          serv = event.selectedService;
        }

        if(serv == this.services[i]._id){
          serviceTemp = this.services[i];
        }
      }

      if(typeof serviceTemp!='undefined') {

        var enddate = moment(event.start).add(serviceTemp.duration, 'minutes').toDate();

        // this.tempEvents[0].end = enddate;
        event.end = enddate;

      }


    });

    // this.refresh.next();
  }

  handleHourClicked(event,event2) {
    
    // //console.log("Clicked on hour");

    if(this.tempEvents.length > 0) {
      this.tempEvents[0].start = event.date;
      this.startDateChange(this.tempEvents[0], null);
      this.refresh.next();
    }
  }

  calculateEndDateTime(event) {

    var serviceTemp = null;
    for(var i=0; i< this.services.length; i++){
      if(this.selectedService == this.services[i]._id){
        serviceTemp = this.services[i];
      }
    }

    var enddate = moment(this.tempEvents[0].start).add(serviceTemp.duration, 'minutes').toDate();

    // //console.log(event.start)
    // //console.log(serviceTemp.duration)
    // //console.log(enddate)

    this.tempEvents[0].end = enddate;

    // this.tempEvents[0] = event;

    this.refresh.next();

  }

  endDateChange(event,startDate){
    // //console.log("asf404ru3w4tyqwhyasdfahjlksdfna")
  }

}


